import React from "react"

const loves = [
    "sorting 'alphabet'",
    "building 'blocks'",
    "'quantum physics'",
    "playing GO",
    "folding origami",
    "playing soccer",
    "painting"
]
const WilliamAge = ()=> (<span>
    {"" + ((new Date()).getFullYear() - (new Date(2015, 4, 2)).getFullYear())}
    {`-years-old son just loves ${loves[Math.floor(Math.random()*loves.length)]}`}
</span>)

export default WilliamAge

